  font-size: 40px;
  color: white;
}

.projects-categories {
  width: 100%;
  height: 140px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.buttons-list {
  display: flex;
  width: 80%;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.buttons {
  width: 20%;
