  color: black;
}

.button-up {
  border-radius: 9px 9px 0px 0px;
  background: black;
  color: rgb(189, 177, 177);
}

.btn-border {
  border-top: 2px solid rgb(252, 176, 90);
  border-radius: 0px 6px 0px 0px;
  border-right: 2px solid rgb(252, 176, 90);
}
.btn-border1 {
  border-bottom: 2px solid white;
  border-radius: 0px 0px 6px 0px;
  border-right: 2px solid white;
}

.btn-border1 a:hover,
.btn-border a:hover {
